html {
  -webkit-text-size-adjust: none;
  /*font-family: "Fu_M", "Noto Sans Korean";*/
}
body, header, footer, #wrap, #contents {
  font-family: 'Nanum Barun Gothic', dotum, '经典圆体简', 'sans-serif';
  color: #414141;
}
html, body, div, header, section, article, span, object, iframe, h1, h2,
h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite,
code, del, dfn, em, img, ins, kbd, q, samp, small, strike, strong, sub, sup,
tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, hr {
  margin: 0;
  padding: 0;
  font-size: 100%;
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
body {
  font-family: 'Nanum Barun Gothic', dotum, '经典圆体简', 'sans-serif';
  font-size: 13px;
  color: #666;
  *word-break: break-all;
  -ms-word-break: break-all;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}
ol, ul, li {
  list-style: none;
}
table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  /*font-family: "Noto Sans Korean"*/
}
form, fieldset, iframe {
  display: block;
  border: 0;
}
button {
  padding: 0;
  margin: 0;
  border: 0;
}
img {
  border: 0 none;
  vertical-align: middle;
  -webkit-perspective: 1px;
}
i, em, address {
  font-style: normal;
}
label, button {
  cursor: pointer;
}
blockquote, q {
  quotes: none;
}
caption, legend, .blind {
  overflow: hidden;
  position: absolute;
  top: -9999em;
  left: -9999em;
  width: 0;
  height: 0;
  padding: 0;
  margin: 0;
  font-size: 0;
  line-height: 0;
  text-indent: -9999em;
  visibility: hidden;
  outline: none;
  z-index: -1;
}
header, footer, section, article, aside, nav, details, menu, figure,
figcaption {
  display: block;
}
button {
  border: none;
  background: transparent;
  cursor: pointer;
  width: auto;
  overflow: visible;
  display: inline-block;
  zoom: 1;
}
button::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}
input, textarea, select, button {
  color: #666;
  vertical-align: middle;
  font-family: 'Nanum Barun Gothic', '经典圆体简', sans-serif;
}
input[type='text'], input[type='password'], input[type='submit'],
input[type='search'] {
  -webkit-appearance: none;
  border: 1px solid #666;
  border-radius: 0;
}
input:checked[type='checkbox'] {
  background-color: #666;
  -webkit-appearance: checkbox;
}
input[type='button'], input[type='submit'], input[type='reset'],
input[type='file'] {
  -webkit-appearance: button;
  border: 1px solid #666;
  border-radius: 0;
}
input[type='search']::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
textarea {
  line-height: 1.2;
  resize: none;
}
.ellipsis {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.hidden {
  visibility: hidden;
  width: 0;
  height: 0;
}
a {
  text-decoration: none;
}
hr {
  border: 0;
}
.background-gray {
  background: #f5f5f5;
}
textarea:focus, input:focus {
  outline: none;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
button[type="button"], button[type="submit"] {
  width: 154px;
  height: 40px;
  border-radius: 25px;
  font-size: 14px;
}
::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #ccc;
}
::-moz-placeholder {
  /* Firefox 19+ */
  color: #ccc;
}
:-ms-input-placeholder {
  /* IE 10+ */
  color: #ccc;
}
:-moz-placeholder {
  /* Firefox 18- */
  color: #ccc;
}
.stop-scrolling {
  height: 100%;
  overflow: hidden;
}
