@-webkit-keyframes span-first-on {
0% {
    -webkit-transform: translate(-50%, -300%);
            transform: translate(-50%, -300%);
}
30% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
100% {
    -webkit-transform: translate(0, 275%) rotate(-45deg);
            transform: translate(0, 275%) rotate(-45deg);
}
}
@keyframes span-first-on {
0% {
    -webkit-transform: translate(-50%, -300%);
            transform: translate(-50%, -300%);
}
30% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
100% {
    -webkit-transform: translate(0, 275%) rotate(-45deg);
            transform: translate(0, 275%) rotate(-45deg);
}
}
@-webkit-keyframes span-first-off {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}
30% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
100% {
    -webkit-transform: translate(-50%, -300%);
            transform: translate(-50%, -300%);
}
}
@keyframes span-first-off {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}
30% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
100% {
    -webkit-transform: translate(-50%, -300%);
            transform: translate(-50%, -300%);
}
}
@-webkit-keyframes span-second-on {
0% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
50% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
100% {
    -webkit-transform: translate(150%, -50%) scale(0);
            transform: translate(150%, -50%) scale(0);
}
}
@keyframes span-second-on {
0% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
50% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
100% {
    -webkit-transform: translate(150%, -50%) scale(0);
            transform: translate(150%, -50%) scale(0);
}
}
@-webkit-keyframes span-second-off {
0% {
    -webkit-transform: translate(-150%, -50%) scale(0);
            transform: translate(-150%, -50%) scale(0);
}
50% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
100% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
}
@keyframes span-second-off {
0% {
    -webkit-transform: translate(-150%, -50%) scale(0);
            transform: translate(-150%, -50%) scale(0);
}
50% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}
100% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
}
@-webkit-keyframes span-third-on {
0% {
    -webkit-transform: translate(-50%, 200%);
            transform: translate(-50%, 200%);
}
30% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
100% {
    -webkit-transform: translate(0, -250%) rotate(45deg);
            transform: translate(0, -250%) rotate(45deg);
}
}
@keyframes span-third-on {
0% {
    -webkit-transform: translate(-50%, 200%);
            transform: translate(-50%, 200%);
}
30% {
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
100% {
    -webkit-transform: translate(0, -250%) rotate(45deg);
            transform: translate(0, -250%) rotate(45deg);
}
}
@-webkit-keyframes span-third-off {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}
30% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
100% {
    -webkit-transform: translate(-50%, 200%);
            transform: translate(-50%, 200%);
}
}
@keyframes span-third-off {
0% {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}
30% {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
}
100% {
    -webkit-transform: translate(-50%, 200%);
