.below-table-menu {
  position: fixed;
  top: 0;
  right: 0;
  display: none;
  width: 0;
  height: 100%;
  overflow: hidden;
  z-index: 1;
  background: #00b1ad;
  background: -webkit-gradient(linear, left top, left bottom, from(#00b1ad), to(#5e35e3));
  background: linear-gradient(to bottom, #00b1ad 0%, #5e35e3 100%);
  -webkit-animation: menu-off 0.1s ease-in-out;
          animation: menu-off 0.1s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.below-table-menu.active {
    display: block;
    -webkit-animation: menu-on 0.1s ease-in-out;
            animation: menu-on 0.1s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes menu-on {
from {
    width: 0;
}
to {
    width: 100%;
}
}
@keyframes menu-on {
from {
    width: 0;
}
to {
    width: 100%;
}
}
@-webkit-keyframes menu-off {
from {
    width: 100%;
}
to {
    width: 0;
}
}
@keyframes menu-off {
from {
    width: 100%;
}
to {
    width: 0;
}
}
