.server-error {
  width: 500px;
  margin: 100px auto 0;
  padding: 50px 0 30px;
}
.server-error .icon-error {
    display: block;
    margin: 0 auto;
}
.server-error h1 {
    margin: 44px 0 0;
    font-size: 28px;
    font-weight: bold;
    color: #414141;
    text-align: center;
    line-height: 1.8;
}
@media screen and (max-width: 640px) {
.server-error h1 {
        font-size: 20px;
}
}
.server-error .error-message {
    margin: 27px 0 0;
    font-size: 18px;
    color: #414141;
    text-align: center;
    line-height: 1.6;
}
@media screen and (max-width: 640px) {
.server-error .error-message {
        padding: 0 10%;
        font-size: 14px;
}
}
.server-error .page-move {
    width: 172px;
    margin: 35px auto 0;
}
.server-error .page-move li {
      padding: 20px 0 0;
}
.server-error .button-prev {
    display: block;
    width: 172px;
    height: 32px;
    background: #00b1ad;
    font-size: 14px;
    line-height: 32px;
    color: #fff;
    border-radius: 25px;
    text-align: center;
}
.server-error .button-home {
    display: block;
    width: 172px;
    height: 32px;
    border: 2px solid #00b1ad;
    font-size: 14px;
    line-height: 32px;
    color: #00b1ad;
    border-radius: 25px;
    text-align: center;
}
@media screen and (max-width: 640px) {
.server-error {
      width: 100%;
      margin: 0;
      padding: 0 0 30px;
      background: #fff;
}
}
.warning-popup {
  position: absolute;
  top: 100px;
  left: 50%;
  margin: 0 0 0 -472px;
  -webkit-box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1);
}
.warning-popup img {
    display: inline-block;
    width: 290px;
    height: 721px;
}
@media screen and (min-width: 640px) and (max-width: 1024px) {
.warning-popup {
      margin: 0 0 0 -46%;
}
}
@media screen and (max-width: 640px) {
.warning-popup {
      margin: 0 0 0 -145px;
}
}
.warning-popup .warning-popup-close {
    position: absolute;
    top: 20px;
    right: 20px;
    display: block;
    width: 20px;
    height: 20px;
    text-indent: -9999em;
}

